import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Projects from "../components/projects"

const ProjectsPage = () => (
  <Layout>
    <SEO title="Jeremy Bernier - Projects" />
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `.3rem 1.0875rem`,
      }}
    >
      <div style={{marginTop: '1rem'}}>
      	<p>Here are some of the projects I've worked on in my life as a full stack engineer.</p>
      	<p>Check out my <a href="https://linkedin.com/in/jeremysbernier/">LinkedIn</a> for my full work history, or my <a href="/resume.pdf">resume</a> for something simpler.</p>
      </div>
    </div>
    <Projects />
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `.3rem 1.0875rem`,
      }}
    >
      <div>
      	<p>Check out my <a href="https://linkedin.com/in/jeremysbernier/">LinkedIn</a> for my full work history, or my <a href="/resume.pdf">resume</a>.</p>
      </div>
    </div>
  </Layout>
)

export default ProjectsPage
